import { PlusOutlined } from '@ant-design/icons';
import { Modal, Upload, Form, Button, Space, Tooltip } from 'antd';
import type { RcFile, UploadProps } from 'antd/es/upload';
import type { UploadFile } from 'antd/es/upload/interface';
import React, { useState } from 'react';
import { UploadOutlined, ExpandOutlined } from '@ant-design/icons';

declare const chrome: any;

const getBase64 = (file: RcFile): Promise<string> =>
  new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result as string);
    reader.onerror = error => reject(error);
  });

const Evidence: React.FC = () => {
  const [previewOpen, setPreviewOpen] = useState(false);
  const [previewImage, setPreviewImage] = useState('');
  const [previewTitle, setPreviewTitle] = useState('');
  const [fileList, setFileList] = useState<UploadFile[]>([
    {
      uid: '-3',
      name: 'image.png',
      status: 'done',
      url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
    },
    {
      uid: '-xxx',
      percent: 50,
      name: 'image.png',
      status: 'uploading',
      url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
    },
    // {
    //   uid: '-5',
    //   name: 'image.png',
    //   status: 'error',
    // },
  ]);

    const handleCancel = () => setPreviewOpen(false);

    const handlePreview = async (file: UploadFile) => {
        if (!file.url && !file.preview) {
        file.preview = await getBase64(file.originFileObj as RcFile);
        }

        setPreviewImage(file.url || (file.preview as string));
        setPreviewOpen(true);
        setPreviewTitle(file.name || file.url!.substring(file.url!.lastIndexOf('/') + 1));
    };

    const handleChange: UploadProps['onChange'] = ({ fileList: newFileList }) =>
        setFileList(newFileList);

    const uploadButton = (
        <div>
        <PlusOutlined />
        <div style={{ marginTop: 8 }}>Upload</div>
        </div>  
    );

    const takeScreenshot = () => {
        console.log('runtime', chrome.runtime.sendMessage)
        chrome.runtime.sendMessage({type: "take-screenshot"}, (response: any) => {
            console.log('message cb', response);
        });
    }

    const getLabel = () => {
        return (<Space 
                style={{paddingBottom: '10px'}}
            >
            <b>Upload evidences</b>
            <Tooltip title="Upload image">
                <Upload><Button type="primary" shape="circle" icon={<UploadOutlined />} size='middle' /></Upload>
            </Tooltip>
            <Tooltip title="Screenshot website">
                <Button onClick={takeScreenshot} type="primary" shape="circle" icon={<ExpandOutlined />} size='middle' />
            </Tooltip>

        </Space>)
    }

  return (
    <Form.Item
        label={getLabel()}
        name="evidences"
        rules={[{ required: true }]}
    >
        <Upload
            action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
            listType="picture-card"
            fileList={fileList}
            onPreview={handlePreview}
            onChange={handleChange}
            style={{height: 'auto'}}
        >
        </Upload>

        <Modal open={previewOpen} title={previewTitle} footer={null} onCancel={handleCancel}>
            <img alt="example" style={{ width: '100%' }} src={previewImage} />
        </Modal>
    </Form.Item>
  );
};

export default Evidence;